.loader {
    background: url(../images/server.jpg) #E4DCD4;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.loader__image {
    width: 250px;
    height: 250px;
    margin-bottom: 20px;
}

.__img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.loader__title {
    color: #fa1212;
    font-size: 2.1rem;
    font-weight: 700;
    margin-bottom: 30px;
    position: relative;
}

.loader__progress {
    width: 200px;
    max-width: 400px;
    height: 4px;
    width: 50%;
    background: rgb(212, 238, 44);
    position: relative;
}

.loader__progress::after {
    content: "";
    position: absolute;
    height: 100%;
    background: rgb(0, 216.5, 187.0288);
    width: 0%;
    transition: width 3s linear;
}

.loader__progress--done::after {
    width: 100%;
}

.loader_2 {
    color: #fa1212;
}